// frontend/pages/messages/index.js
import { useEffect, useState, useRef } from "react";
import api from "../../utils/api";
import { useRouter } from "next/router";
import socket from "../../utils/socket";

export default function Messages() {
  const router = useRouter();
  const { post } = router.query;

  const [messages, setMessages] = useState([]);
  const [text, setText] = useState("");
  const [userId, setUserId] = useState(null);
  const bottomRef = useRef(null);

  // -------------------- LOCAL STORAGE POST SHARE --------------------
  const getSharedPosts = () => {
    if (typeof window === "undefined") return [];
    const saved = localStorage.getItem("sharedPosts");
    return saved ? JSON.parse(saved) : [];
  };

  const addSharedPost = (postId) => {
    const shared = getSharedPosts();
    if (!shared.includes(postId)) {
      shared.push(postId);
      localStorage.setItem("sharedPosts", JSON.stringify(shared));
    }
  };

  const isAlreadyShared = (postId) => getSharedPosts().includes(postId);

  // -------------------- LOAD USER --------------------
  useEffect(() => {
    const id = localStorage.getItem("userId");
    if (!id) {
      router.push("/login");
      return;
    }
    setUserId(id);

    socket.emit("join_room", id);
  }, []);

  // -------------------- AUTO SCROLL --------------------
  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // -------------------- LOAD MESSAGES --------------------
  useEffect(() => {
    if (userId) loadMessages();
  }, [userId]);

  const loadMessages = async () => {
    try {
      const res = await api.get(`/messages/${userId}`);
      setMessages(res.data);
    } catch (err) {
      console.error(err);
    }
  };

  // -------------------- LISTEN TO REAL-TIME MESSAGES --------------------
  useEffect(() => {
    socket.on("new_message", (msg) => {
      setMessages((prev) => [...prev, msg]);
    });

    return () => socket.off("new_message");
  }, []);

  // -------------------- SHARE POST ONCE --------------------
  useEffect(() => {
    if (!userId || !post) return;
    if (isAlreadyShared(post)) return;

    const sharePost = async () => {
      try {
        const res = await api.get(`/posts/${post}`);

        const msgData = {
          userId,
          text: `Shared a post: ${res.data.title} (${res.data.price} BDT)`,
          type: "shared_post",
          postId: res.data._id,
          sender: "user",
        };

        const sent = await api.post("/messages/send", msgData);
        socket.emit("send_message", sent.data);

        setMessages((prev) => [...prev, sent.data]);

        addSharedPost(post);
      } catch (err) {
        console.log("Share failed:", err);
      }
    };

    sharePost();
  }, [userId, post]);

  // -------------------- SEND TEXT MESSAGE --------------------
  const sendMessage = async () => {
    if (!text.trim()) return;

    const msgData = {
      userId,
      text,
      type: "text",
      sender: "user",
    };

    const sent = await api.post("/messages/send", msgData);
    socket.emit("send_message", sent.data);

    setMessages((prev) => [...prev, sent.data]);

    setText("");
  };

  // -------------------- UI --------------------
  return (
    <div className="h-screen flex flex-col bg-gray-100">
      <div className="bg-blue-600 text-white p-4 flex items-center justify-between">
        <h2 className="text-xl font-semibold">Chat with Admin</h2>
        <a href="/" className="bg-white text-blue-700 px-3 py-1 rounded-lg shadow">
          Home
        </a>
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-3">
        {messages.map((m) => (
          <div key={m._id} className={m.sender === "user" ? "text-right" : "text-left"}>
            {m.type === "shared_post" ? (
              <div
                onClick={() => router.push(`/posts/${m.postId}`)}
                className="inline-block bg-gray-200 px-3 py-2 rounded-lg cursor-pointer"
              >
                {m.text}
              </div>
            ) : (
              <div
                className={`inline-block px-4 py-2 rounded-xl shadow ${
                  m.sender === "user" ? "bg-blue-500 text-white" : "bg-white text-gray-900"
                }`}
              >
                {m.text}
              </div>
            )}
          </div>
        ))}

        <div ref={bottomRef}></div>
      </div>

      <div className="p-3 border-t bg-white flex items-center gap-2">
        <input
          className="flex-1 border p-3 rounded-xl bg-gray-100"
          placeholder="Type..."
          value={text}
          onChange={(e) => setText(e.target.value)}
        />

        <button
          onClick={sendMessage}
          className="bg-blue-600 text-white px-5 py-3 rounded-xl shadow"
        >
          Send
        </button>
      </div>
    </div>
  );
}
